@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Mukta&family=Poppins&family=Ysabeau+SC&family=Roboto+Mono&display=swap');

body {
  margin: 0;
  font-family: 'Roboto Mono', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  @apply flex flex-col items-center;
}

#root {
  @apply w-full max-w-screen-2xl;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

form input,
form select {
  @apply p-2 sm:p-4 outline-none border border-gray-300 my-1 rounded-sm text-sm sm:text-base mb-2;
}

label {
  @apply text-brand font-semibold;
}
